<template>
  <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
      prop="over_odds"
      :label="home_name"
      align="center"
      >
    </el-table-column>
    <el-table-column
      prop="over_name"
      label="盘口"
      align="center"
      >
    </el-table-column>
    <el-table-column
      prop="under_odds"
      :label="away_name"
      align="center"
      >
    </el-table-column>
    <el-table-column
      prop="add_time"
      label="变化时间"
      align="center"
      width="250">
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'asia',
  data () {
    return {
      home_name: '大球',
      away_name: '小球',
      tableData: [
        {
          over_odds: '1.800',
          over_name: '3.333',
          under_odds: '2.800',
          under_name: '让半球',
          add_time: '2020-10-20 17:00:20'
        },
        {
          over_odds: '1.800',
          over_name: '3.333',
          under_odds: '2.800',
          under_name: '让半球',
          add_time: '2020-10-20 17:00:20'
        }
      ]
    }
  }
}
</script>

<style scoped>
</style>



